.list {
    width: 600px;
    padding-top: 10px;
    padding-left: 50px;
    padding-bottom: 20px;
    margin: auto;
}

.listItem {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 5px;
    margin-right: 120px;

    border-bottom: 2px solid black;
}

.contactName {
    font-size: 16px;
}

.contactBtn {
    height: 21px;
    width: 90px;
    cursor: pointer;
}